        formatInvoiceAddress(addressObj) {
          debug.log(addressObj);
          const addressLabels = {
            "officE_NO": "",
            "officE_MOO": "",
            "officE_ROAD_TH": "",
            "officE_SOI_TH": "",
            "officE_BUILDING_TH": "/",
            "officE_SUB_DISTRICT_TH": "/",
