      downloadSampleCSV() {
        const headers = 'bu_name,bu_type,prefix,firstname,lastname,position,phone,email\n';
        const sampleData = [
          '  ,,,,,,0891234567,user1@example.com',
          '  ,,,,,,0891234568,user2@example.com'
        ].join('\n');
        
        const csvContent = headers + sampleData;
        const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        const url = URL.createObjectURL(blob);
        
        link.setAttribute('href', url);
        link.setAttribute('download', 'enroll_template.csv');
        link.style.visibility = 'hidden';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      },
      generateRandomPassword(index) {
        const length = 12;
        const charset = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789!@#$%&";
        let password = "";
        for (let i = 0; i < length; i++) {
          const randomIndex = Math.floor(Math.random() * charset.length);
          password += charset[randomIndex];
        }
        this.manualInput[index].password = password;
      },
